.active-stations {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	max-width: 70%;
	margin: 0 auto 20px auto;
	padding: 10px;
	border: 1px solid red;
	border-radius: 10px;
}

.active-stations__info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	column-gap: 15px;
	row-gap: 15px;
}

.active-stations__describe {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}

.active-stations__buttons {
	display: flex;
	column-gap: 20px;
}

.active-stations__img {
	max-width: 250px;
}
